.container{
  display:flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #dec9e9;
  text-align: center;
}

.card{
  border-radius: 1em;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.6);
  padding: 0.8em 1em;
  background-color:white;
}

.flex-container{
  display: flex;
}

.flex-container > *{ flex:1 }

.form-control{
  width: 100%;
  display: block;
  border:0;
  font-size: 1em;
  padding: 0.8em 1em;
  box-sizing: border-box;
  border-radius: 0.4em;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.6);
  margin-top: 1.5em;
}

.app-button{
  font-size: 1em;
  padding: 0.8em 1em;
  box-sizing: border-box;
  border-radius: 0.4em;
  border: 0;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.6);
  background-color: #bc00dd;
  color: white;
  margin-top: 1em;
}
